footer {
    display: grid;
    align-items: end;
    grid-template-columns: 40px 40px 40px auto 85px;
    grid-gap: 1em;
    margin:32px 0px 10px 0px;
}

@media only screen and (min-width: 768px) {
    footer {
        position: absolute;
        bottom: 0px;
    }    
}



/* --------------- SOCIAL MEDIA ----------------- */
.social{
    width: 24px;
    padding: 5px;
    transition: all 0.3s ease-in-out;
    filter: grayscale(100);
}
  
.social:hover{
    width: 32px;
    margin-left:-4px;
    margin-top:-4px;
    transition: all 0.3s ease-in-out;
    filter: grayscale(0);
}
  
  
