header {
    box-sizing: border-box;
    margin: 0px;
    padding: 10px 24px 10px 24px;
    display: grid;
    grid-template-columns: auto 20px;
    background-color: white;
    box-shadow: 1px 2px 4px rgba(0, 0, 0, .5);
    width: 100%;
    position: fixed;
    top:0px;
    z-index: 150;
}